<script lang="ts">
  import { distance, genre } from '../stores'
  import { activePage, activeFilters } from '../helpers'
  import { getNearbyArtists, getNearbyGenres } from '../api/nearby'

  import ArtistCard from '../components/ArtistCard.svelte'

  let loadingText = 'Finding Artists...'
  let promise = getNearbyArtists($distance, $genre).catch(() => {
    loadingText = 'Please enable location services'
  })
  let genrePromise = promise
  $: genresPromise = getNearbyGenres($distance, genrePromise)

  activePage('discover')

  async function onClick(e, miles: number, selectedGenre) {
    distance.set(miles)
    genre.set(selectedGenre)
    promise = getNearbyArtists($distance, $genre)
    if (e.target.classList.contains('distance')) {
      genrePromise = getNearbyArtists($distance, 'all')
    }
    activeFilters(e)
  }
</script>

<h1 class="page-title text-center">Artists Near You</h1>
<div class="d-flex justify-content-center">
  <!-- svelte-ignore a11y-invalid-attribute -->
  <a
    href="#"
    on:click={(e) => onClick(e, 10, 'all')}
    class="badge m-5 distance activeDistance">10 miles</a
  >
  <!-- svelte-ignore a11y-invalid-attribute -->
  <a href="#" on:click={(e) => onClick(e, 25, 'all')} class="badge m-5 distance"
    >25 miles</a
  >
  <!-- svelte-ignore a11y-invalid-attribute -->
  <a href="#" on:click={(e) => onClick(e, 50, 'all')} class="badge m-5 distance"
    >50 miles</a
  >
  <!-- svelte-ignore a11y-invalid-attribute -->
  <a
    href="#"
    on:click={(e) => onClick(e, 25000, 'all')}
    class="badge m-5 distance">Everywhere</a
  >
</div>
{#await genresPromise then genres}
  <div class="d-flex justify-content-center flex-wrap">
    <!-- svelte-ignore a11y-invalid-attribute -->
    <a
      href="#"
      on:click={(e) => onClick(e, $distance, 'all')}
      class="badge m-5 genre activeGenre">all</a
    >
    {#each genres as genre}
      <!-- svelte-ignore a11y-invalid-attribute -->
      <a
        href="#"
        on:click={(e) => onClick(e, $distance, genre)}
        class="badge m-5 genre">{genre}</a
      >
    {/each}
  </div>
{/await}
{#await promise}
  <p class="loading">{loadingText}</p>
{:then artists}
  {#if artists && artists.length == 0}
    <p class="text-center">No Artists Found Nearby</p>
  {:else if artists}
    {#each artists as artist}
      <ArtistCard {artist} />
    {/each}
  {/if}
{/await}
